.b-log-in-block {
	&__button {
		margin-bottom: 22px; }
	&__accent {
		color: $accent; }
	&__warn {
		color: $addition-darkness-color;
		margin-bottom: 0; }

	@media screen and (max-width: $max-sm) {
		&__button {
			display: flex;
			justify-content: center; } }


	@media screen and (min-width: $lg) {
		&__caption {
			font-size: 27px; } } }
