.b-navigation {
	@include row;
	flex-direction: column;
	font: 28px $addition-font;
	&__item {
		padding-bottom: 35px;

		&:last-child {
			padding-right: 0; }

		&:hover {
			color: #d05685; } }

	///.b-navigation
	@media screen and (min-width: $lg) {
		font: 16px $default-font;
		flex-direction: row;
		align-items: center;

		&__item {
			padding-right: 2.3em;
			padding-bottom: 0; } } }
