.b-photo-work {
	position: relative;
	display: block;
	overflow: hidden;
	padding: 1px;
	&__icon {
		position: absolute;
		top: 20px;
		left: 20px;
		color: #ffffff;
		font-size: 32px; }

	&__voted {
		font: 24px $addition-font;
		position: absolute;
		right: 0;
		bottom: 23px;
		background-color: #ffffff;
		padding: 15px 20px;
		border-top-left-radius: 2em;
		border-bottom-left-radius: 2em;
		@include box-shadow(0, 1px, 5px, 0, rgba(0, 0, 0, 0.25));
		&--opacity {
			.b-photo-work__voted-icon,
			.b-photo-work__voted-count {
				opacity: .5; } } }
	&__voted-icon {
		margin-right: 10px;
		display: inline-block;
		vertical-align: middle; }

	&__voted-count {
		color: $accent;
		background-image: linear-gradient(to right, #ea8e99 0%, #c9477f 100%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent; } }
