body,
html {
  height: 100%;
  margin: 0;
}

body {
  font: 500 16px/1.3 "MuseoSans", Helvetica, Arial, sans-serif;
  color: #000000;
}

body * {
  box-sizing: border-box;
  outline: none;
}

body *:hover, body *:focus {
  outline: none;
}

body.modal-open {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #000000;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Roska", Helvetica, Arial, sans-serif;
  font-weight: 500;
  margin-top: 0;
  margin-bottom: 20px;
}

h6 {
  margin-bottom: 12px;
}

.h1 {
  font-size: 34px;
}

.h2 {
  font-size: 26px;
}

.h3 {
  font-size: 22px;
}

.h4 {
  font-size: 18px;
}

.h5, .h6 {
  font-size: 18px;
}

p {
  margin-top: 0;
  margin-bottom: 15px;
}

button, input, textarea, .e-input {
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

input, textarea, .e-input {
  display: block;
  border: 2px solid transparent;
  padding: 13px 11px 11px;
  width: 100%;
  font-size: 16px;
}

input:focus, input:required:valid, textarea:focus, textarea:required:valid, .e-input:focus, .e-input:required:valid {
  border-color: #d05685;
}

input[type="time"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  margin: 0;
  padding: 0;
  width: 0;
  display: none;
}

button {
  font-size: 1em;
  padding: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
}

video {
  max-width: 100%;
}

table {
  width: 100%;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

figure {
  margin: 0;
}

@media screen and (max-width: 1024px) {
  body.menu-open {
    overflow: hidden;
