.b-stages {
	position: relative;
	overflow: hidden;
	color: #fff;
	margin-bottom: 100px;
	&__content {
		position: relative;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		justify-content: space-between; }
	&__content-body {
		background-color: $addition-light-color;
		width: 100%;
		height: 100vh;
		overflow: hidden;
		-webkit-perspective: 1000px;
		perspective: 1000px; }
	&__steps {
		display: flex;
		flex-wrap: wrap;
		align-items: center; }
	&__row {
		display: flex;
		width: 400%;
		height: 100%; }
	&__footer {
		position: absolute;
		bottom: 55px;
		left: 0;
		right: 0; }
	&__date {
		opacity: 0; } }

.b-stage {
	padding: 20px 0 20px;
	position: relative;
	overflow: hidden;
	width: 25%;
	&__row {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }

	&__title {
		margin-bottom: 20px; }
	&__content {
		flex: 0 0 100%;
		max-width: 100%;
		margin-bottom: 80px; }
	&__figure-wrap {
		margin: 0 auto; }
	&__figure {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		align-items: flex-end;
		justify-content: center;
		margin-top: 0;
		max-width: 630px;
		&--end {
			justify-content: flex-end; } }
	&__img-wrap {
		position: relative;
		margin: 6px;
		background-color: $main-silver;

		&--lg {
			width: 288px;
			height: 288px;
			z-index: 1;
			//+translate(18%, 41%)
			@include box-shadow(0, 30px, 60px, 0, rgba(0, 0, 0, 0.15)); }

		&--md {
			width: 264px;
			height: 264px;
			//margin-left: auto
			//margin-right: 75px
			@include box-shadow(0, 30px, 60px, 0, rgba(0, 0, 0, 0.15)); }

		&--sm {
			width: 216px;
			height: 216px;
			//+translate(0px, -32%)
			@include box-shadow(0, 30px, 60px, 0, rgba(0, 0, 0, 0.15)); } }


	&__description {
		position: relative;
		z-index: 1;
		font-size: 20px;
		font-weight: 300; }

	&__likes {
		position: absolute;
		right: -30px;
		top: -70px;
		transform: rotate(-20deg); }
	&__before-camera {
		display: none;
		width: 352px;
		height: 280px;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		z-index: 1;
		background: transparent url("../assets/img/camera.png") center no-repeat;
		@include translate(12%, 24%); }

	&__before-heart {
		position: absolute;
		right: -3%;
		bottom: 12%;
		z-index: 2; }

	&__before-xxxxl {
		top: -18px;
		left: initial;
		right: -474px;
		z-index: 1;
		overflow: hidden;
		@include rotate(10deg); }

	&__before-img {
		@include rotate(-10deg); } }



@media screen and (max-width: $max-lg) {

	.b-stage {
		&__before-img {
			position: absolute;
			top: -23px; }
		&__before-xxxxl {
			//top: initial
			//bottom: 70px
			//left: initial
			//right: -474px
			top: initial;
			bottom: 60px;
			left: initial;
			right: -62px;
			width: 590px;
			height: 365px; }
		&__figure {
			flex-wrap: nowrap;
			margin-left: -100px;
			margin-right: -100px;
			&--in-line-center {

				.b-stage__img-wrap {
					&:nth-child(1) {
						min-width: 150px; }
					&:nth-child(2) {
						min-width: 200px; }
					&:nth-child(3) {
						min-width: 250px; } } }

			&--end {
				.b-stage__img-wrap {
					width: 150px;
					order: 1;
					&:nth-child(1) {
						order: 2;
						min-width: 200px; }
					&:nth-child(2) {
						order: 1;
						min-width: 200px; }
					&:nth-child(3) {
						order: 0;
						min-width: 150px; } } } }

		&__img-wrap {
			&--sm {
				order: 1;
				width: 150px;
				height: 150px; }
			&--md {
				order: 3;
				width: 230px;
				height: 230px; }

			&--lg {
				order: 2;
				width: 250px;
				height: 250px; } } } }

@media screen and (max-width: $max-sm) {
	.b-stages {
		&__footer {
			bottom: 15px; } }
	.b-stage {
		&__before-xxxxl {
			top: initial;
			bottom: 60px;
			left: initial;
			right: -9px;
			width: 300px;
			height: 150px; }
		&__title {
			font-size: 22px;
			margin-bottom: 10px; }

		&__description {
			font-size: 16px; }

		&__content {
			margin-bottom: 0; }
		&__figure-wrap {
			margin: 0 auto 60px;
			flex: 1;
			display: flex; }
		&__figure {
			margin-left: -50px;
			margin-right: -50px;

			&--in-line-center {
				.b-stage__img-wrap {
					&:nth-child(1) {
						min-width: 110px; }
					&:nth-child(2) {
						min-width: 130px; }
					&:nth-child(3) {
						min-width: 150px; } } }

			&--end {
				.b-stage__img-wrap {
					width: 110px;
					&:nth-child(1) {
						min-width: 150px; }
					&:nth-child(2) {
						min-width: 150px; }
					&:nth-child(3) {
						min-width: 110px; } } } }
		&__img-wrap {
			&--sm {
				width: 110px;
				height: 110px; }
			&--md {
				width: 130px;
				height: 130px; }

			&--lg {
				width: 150px;
				height: 150px; } }
		&__container,
		&__row {
			height: 100%; } } }

@media screen and (min-width: $lg) {
	.b-stages {
		margin-bottom: 200px;
		&__content {
			padding: 60px 0 60px; } }

	.b-stage {
		&__before-camera {
			display: block; }
		&__content {
			flex: 0 0 44%;
			max-width: 44%;
			margin-bottom: 0; }
		&__description {
			font-size: 30px; }
		&__figure-wrap {
			flex: 0 0 56%;
			max-width: 56%; }
		&__figure {
			margin-top: -76px;
			margin-left: auto; }

		&__img-wrap {
			margin: 12px;

			&--lg {
				width: 265px;
				height: 265px;
				@include translate(18%, 41%);
				@include box-shadow(0, 30px, 60px, 0, rgba(0, 0, 0, 0.15)); }

			&--md {
				width: 240px;
				height: 240px;
				margin-left: auto;
				margin-right: 75px;
				@include box-shadow(0, 30px, 60px, 0, rgba(0, 0, 0, 0.15)); }

			&--sm {
				width: 190px;
				height: 190px;
				@include translate(0px, -32%); } }

		&__before-heart {
			bottom: 35%; } } }
@media screen and (min-width: 1200px) {
	.b-stage {
		&__img-wrap {
			&--lg {
				width: 288px;
				height: 288px; }

			&--md {
				width: 264px;
				height: 264px; }

			&--sm {
				width: 216px;
				height: 216px; } } } }
