.e-likes {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	font: 27px $addition-font;
	color: #ffffff;
	background-color: $accent-light;
	width: 136px;
	height: 55px;
	@include box-shadow(0, 20px, 40px, 0, rgba(234, 142, 153, 0.5));
	@include border-radius(.45em);
	&:before {
		content: '';
		position: absolute;
		top: 99%;
		left: 0;
		right: 0;
		display: block;
		margin: 0 auto;
		width: 0;
		height: 0;
		border-left: 16px solid transparent;
		border-right: 16px solid transparent;
		border-top: 16px solid $accent-light; }

	&__count {
		margin-top: -13px;
		margin-left: 10px; }

	//.e-likes
	@media screen and (max-width: $max-lg) {
		svg {
			width: 29px;
			height: 26px; } }
	@media screen and (min-width: $lg) {
		font-size: 36px;
		width: 180px;
		height: 80px;
		&:before {
			border-left: 20px solid transparent;
			border-right: 20px solid transparent;
			border-top: 20px solid $accent-light; }
		&__count {
			margin-left: 15px; } } }
