.b-map {
	position: relative;
	@include rotate(-10deg);
	border-top-left-radius: 20em;
	overflow: hidden;
	height: 1070px;
	bottom: -250px;
	left: -50px;
	width: 150%;
	&__inside-wrap {
		width: 150%;
		transform: rotate(10deg) translate(-23%, 0); }
	&__item {
		height: 900px;
		width: 100%;
		overflow: initial !important; } }
