.o-modal {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: #ffffff;
	z-index: 100;
	opacity: 0;
	visibility: hidden;
	transform: translate(100%, 0) scale(.8);
	@include transition(all, .4s, linear);
	&.active {
		opacity: 1;
		visibility: visible;
		transform: translate(0, 0) scale(1);
		display: block; }
	p {
		line-height: 1.4; }
	&__center {
		height: 100%;
		min-height: 100%;
		//text-align: center
		//padding: 2%
		overflow-y: auto;
		//&:before
		//	content: ''
		//	display: inline-block
		//	vertical-align: middle
		//	height: 100%
 }		//	margin-right: -2%

	&__content {
		overflow: hidden;
		position: relative;
		display: inline-flex;
		vertical-align: middle;
		background-color: #ffffff;
		width: 100%;
		padding: 42px 0;
		text-align: left;
		z-index: 101;

		//align-content: center
		//justify-content: center
 }		//flex-direction: column
	&__content-block {
		margin-bottom: 46px; }

	&__caption {
		margin-bottom: 40px;
		color: $accent; }
	&__sub-caption {
		color: $accent; }
	&__close {
		position: absolute;
		right: 0;
		top: 12px;
		z-index: 110;
		cursor: pointer; }
	&__preview {
		position: relative;
		max-width: 420px;
		margin: 0 auto 40px;
		text-align: center; }
	&__preview-icon {
		position: absolute;
		top: 20px;
		left: 20px;
		opacity: .75;
		svg {
			font-size: 32px; } }
	&__before-md-1 {
		@include rotate(35deg);
		width: 459px;
		height: 161px;
		left: -290px;
		top: -50px; }
	&__before-md-2 {
		left: calc(100% - 62px);
		top: -25px; }
	&__item {
		flex: 0 0 100%;
		max-width: 100%; }
	@media screen and (min-width: $sm) {
		&__before-md-2 {
			top: -60px; } }
	@media screen and (min-width: $sm-l) {

		&__preview {
			position: relative;
			height: 0;
			max-width: 100%;
			margin-top: -42px;
			margin-bottom: 0; }
		&__item {
			&--sm {
				flex: 0 0 43%;
				max-width: 43%; }

			&--md {
				flex: 0 0 57%;
				max-width: 57%;
				padding-right: 3em; } } }

	@media screen and (min-width: $lg) {
		&__before-md-2 {
			top: -90px; }
		&__close {
			right: 12px; } } }
