.b-prizes {
	margin-bottom: 100px;
	overflow: hidden;
	@media screen and (min-width: $lg) {
		margin-bottom: 148px; }
	&__container {
		padding: 0 20px; } }

.b-prize {
	position: relative;
	padding: 15px;
	margin-bottom: 25px;
	flex: 0 0 100%;

	&--custom-padd-left {
		padding-left: 30px; }
	&--custom-padd-right {
		padding-right: 30px; }
	&--margin-top {
		margin-top: 10px; }
	&__inside-wrap {
		position: relative;
		overflow: hidden;
		padding: 1px 1px 60px 1px; }
	&--custom-position {
		padding-right: 30px; }

	&__title {
		position: absolute;
		top: 0;
		left: -70px;

		transform: rotate(90deg) translate(53%, 0px);

		&--right {
			left: initial;
			right: -70px; } }
	&--custom-padd-left {
		.b-prize__title {
			left: -50px; } }
	&--custom-padd-right {
		.b-prize__title {
			left: initial;
			right: -110px; } }
	&__icon-wrap {
		width: 40px;
		height: 40px;
		margin-bottom: 20px;
		svg {
			max-width: 100%;
			max-height: 100%; } }
	&__icon {
		position: absolute;
		top: 15px;
		left: 15px;
		width: 40px;
		height: 40px;
		svg {
			width: 100%;
			height: 100%; }
		&--right {
			left: initial;
			right: 15px; } }

	&__caption {
		font-size: 16px;
		position: relative;
		color: $accent;
		background-image: linear-gradient(to right, #ea8e99 0%, #c9477f 100%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent; }

	&__caption-wrap {
		position: absolute;
		left: 0;
		background-color: #ffffff;
		&:before {
			content: '';
			position: absolute;
			bottom: 99%;
			width: 0;
			height: 0; }
		&--style-1 {
			left: 0px;
			bottom: -23px;
			max-width: 257px;
			padding: 20px 30px 15px 0px;
			border-top-right-radius: 6em;
			&:before {
				left: 0px;

				border-bottom: 0 solid transparent;
				border-top: 18px solid transparent;
				border-left: 178px solid #ffffff; } }
		&--style-2 {
			bottom: 30px;
			max-width: 161px;
			padding: 5px;
			border-top-right-radius: 6em;
			&:before {
				background-color: #ffffff;
				border-top-right-radius: 6em;
				width: 204px;
				height: 172px;
				left: -20%;
				bottom: -52px;
				@include rotate(-5deg); } }
		&--style-3 {
			bottom: 10px;
			max-width: 175px;
			padding: 5px;
			-webkit-border-top-right-radius: 6em;
			border-top-right-radius: 6em;
			&:before {
				left: 0px;
				border-top: 17px solid transparent;
				border-left: 97px solid #ffffff;
				border-bottom: 0 solid transparent; } }
		&--style-4 {
			left: initial;
			right: 0;
			bottom: -15px;
			max-width: 130px;

			padding: 5px;
			border-top-left-radius: 6em;
			&:before {
				background-color: #ffffff;
				border-top-left-radius: 6em;
				width: 220px;
				height: 80px;
				right: -60px;
				bottom: 49px;

				@include rotate(-10deg); } }

		&--style-5 {
			left: initial;
			right: 0;
			bottom: -11px;
			max-width: 145px;
			padding: 5px 30px 5px 5px;
			border-top-left-radius: 6em;
			&:before {
				background-color: #ffffff;
				border-top-left-radius: 6em;
				width: 220px;
				height: 90px;
				right: -64px;
				bottom: 40px;

				@include rotate(10deg); } }

		&--style-6 {
			left: 0px;
			bottom: -23px;
			max-width: 175px;
			padding: 26px 15px 15px 0px;
			border-top-right-radius: 6em;
			&:before {
				display: none; } } }

	@media screen and (min-width: $sm) {
		&--custom-position {
			margin-top: -70px; }
		&--lg {
			flex: 0 0 60%; }

		&--sm {
			flex: 0 0 40%; }


		&--md-1 {
			flex: 0 0 43%; }

		&--md-2 {
			flex: 0 0 46%; } }

	@media screen and (min-width: $lg) {
		padding: 20px;
		&--custom-padd-left {
			padding-left: 60px; }
		&--custom-padd-right {
			padding-right: 60px;
			.b-prize__title {
				right: -140px; } }
		&--custom-position {
			padding-right: 60px; }

		&__title {
			left: -100px;
			&--right {
				right: -100px;
				left: auto; } }

		&__icon {
			width: 60px;
			height: 60px; }

		&__caption {
			font-size: 24px; }

		&__caption-wrap {
			&--style-1 {
				max-width: 399px;
				padding: 38px 30px 15px 0px;
				&:before {
					border-top: 29px solid transparent;
					border-left: 298px solid #ffffff; } }
			&--style-2 {
				max-width: 350px;
				&:before {
					width: 417px;
					height: 170px; } }

			&--style-3 {
				bottom: -11px;
				max-width: 337px;
				&:before {
					border-top: 40px solid transparent;
					border-left: 232px solid #ffffff; } }

			&--style-4 {
				max-width: 245px;
				&:before {
					width: 446px;
					height: 145px;
					right: -63%;
					bottom: 49px; } }

			&--style-5 {
				max-width: 270px;
				&:before {
					width: 446px;
					height: 166px;
					right: -50%;
					bottom: -25px; } }
			&--style-6 {
				max-width: 360px; } } }

	@media screen and (min-width: 1200px) {

		&__icon {
			width: 80px;
			height: 80px; }

		&__caption {
			font-size: 32px; }

		&__caption-wrap {

			&--style-2 {
				&:before {
					width: 446px;
					height: 192px; } }

			&--style-3 {
				max-width: 378px;
				&:before {
					border-top: 51px solid transparent;
					border-left: 283px solid #ffffff; } }

			&--style-4 {
				&:before {
					height: 192px; } }

			&--style-5 {

				&:before {

					height: 192px; } } } } }


