.b-call-date {
	color: #ffffff;
	margin: -44px 0 0 47px;
	&--margin-null {
		margin: 0; }
	&__title {
		font-size: 14px;
		opacity: .55;
		margin-bottom: 3px; }
	&__date {
		font-size: 16px;
		text-transform: uppercase;
		letter-spacing: 1px; }

	@media screen and (max-width: $max-sm) {
		&__date {
			font-size: 13px; } }
	@media screen and (max-width: $max-lg) {
		&--desctop {
			display: none; } } }
