.b-contacts {
	position: relative;
	z-index: 1;
	margin-bottom: -200px;
	//background-color: #ffffff

	&__item {
		position: relative;
		flex: 0 0 100%;
		max-width: 100%;
		padding-left: 15px;
		padding-right: 15px;

		margin-bottom: 45px; }

	@media screen and (min-width: $sm) {
		&__item {
			flex: 0 0 50%;
			max-width: 50%; } }
	@media screen and (min-width: $sm-l) {
		&__item {
			flex: 0 0 33.333%;
			max-width: 33.333%; } }
	@media screen and (min-width: $lg) {
		&__item {
			flex: 0 0 25%;
			max-width: 25%; } } }
