.b-preparation {
	margin-bottom: 20px;
	&__part-1 {
		position: relative;
		overflow: hidden;
		margin-bottom: 30px;
		padding-bottom: 130px; }
	&__before-xl {
		top: 15%;
		left: 34%;
		width: 700px;
		opacity: .55;
		@include rotate(-45deg); }
	&__before-xxl {
		top: 28%;
		left: -11%;
		@include rotate(-10deg); }
	&__before-sm {
		top: 90%;
		left: -100px;
		z-index: 1;
		@include rotate(35deg); }
	&__item {
		flex: 0 0 100%;
		max-width: 100%;
		padding-left: 15px;
		padding-right: 15px;
		margin-bottom: 15px; }

	&__before-caption {
		position: absolute;
		top: 85%;
		font-size: 16px;
		left: -26px;
		max-width: 260px;
		padding: 35px 25px;

		background-color: #ffffff;
		@include rotate(35deg);
		@include border-radius(10em); }

	&__before-caption-text {
		text-transform: uppercase;
		color: $accent;
		background-image: linear-gradient(to right, #ea8e99 0%, #c9477f 100%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent; }


	&__part-2 {
		position: relative;
		overflow: hidden;
		padding-bottom: 160px; } }

.b-preparation-gallery {

	&__item {
		text-align: center;
		flex: 0 0 100%;
		max-width: 100%;
		padding: 15px 0;
		margin: 0; }
	&__item-img {
		margin-bottom: 30px; } }

.b-preparation-content {
	&__item {
		padding-left: 15px;
		padding-right: 15px;

		&--md {
			flex: 0 0 100%;
			max-width: 100%; }

		&--sm {
			flex: 0 0 100%;
			max-width: 100%; } }
	&__title {
		font-family: $default-font;
		color: $accent;
		margin-bottom: 10px; }

	&__date {
		font-size: 16px;
		margin-bottom: 20px; } }

@media screen and (min-width: $sm-l) {
	.b-preparation {
		&__before-caption {
			top: 97%;
			left: 2%;
			max-width: 380px;
			padding: 53px 42px; }
		&__part-1 {
			padding-bottom: 0; }

		&__item {
			flex: 0 0 50%;
			max-width: 50%; }

		&__before-caption {
			top: 73%;
			left: 25px; }
		&__before-sm {
			top: 76%;
			left: -150px; } }
	.b-preparation-gallery {

		&__item {
			text-align: center;
			flex: 0 0 50%;
			max-width: 50%;
			&:nth-child(1) {
				padding: 115px 20px 70px 20px; }
			&:nth-child(2) {
				margin-top: 53px; } }

		&__item-img {
			margin-bottom: 73px; } }

	.b-preparation-content {
		&__item {
			padding-left: 15px;
			padding-right: 15px;

			&--sm {
				flex: 0 0 33.333%;
				max-width: 33.333%; } } } }

@media screen and (min-width: $lg) {
	.b-preparation-content {
		&__item {
			&--md {
				flex: 0 0 50%;
				max-width: 50%;
				padding-right: 7%; }
			&--sm {
				flex: 0 0 25%;
				max-width: 25%; } } } }
