.b-about {
	position: relative;

	min-height: 100%;
	min-height: 100vh;
	overflow: hidden;
	background-color: transparent;
	background-repeat: no-repeat;
	background-position: top right;
	background-size: 317px;
	&__before {
		position: absolute;
		top: 27%;
		left: -306px; }

	&__content {
		max-width: 640px;
		padding: 340px 0 100px; }
	&__date {
		margin-bottom: 7px; }
	&__caption {
		margin-bottom: 45px; }
	&__title {
		text-transform: uppercase;
		margin-bottom: 0;
		&--pink {
			color: $accent; } }

	//.b-about
	@media screen and (min-width: $sm) {
		background-size: 53%;

		&__before {
			top: -22%;
			left: -313px; }
		&__content {
			max-width: 360px;
			padding: 195px 0 195px; } }
	@media screen and (min-width: $sm-l) {
		background-size: 57%; }

	@media screen and (min-width: $lg) {

		&__before {
			top: -160px;
			left: -410px; }
		&__caption {
			margin-bottom: 108px; }

		&__content {
			max-width: 640px;
 }			//padding: 195px 0 195px

		&__date {
			margin-bottom: 25px; } }

	@media screen and (min-width: $xl) {

		&__content {
			padding-top: 30vh; } } }
