.b-step-stage {
	position: relative;
	display: flex;
	//display: none
	align-items: flex-end;
	padding: 0;
	flex: 0 0 100%;
	max-width: 100%;
	cursor: pointer;
	opacity: 0;
	&:last-child {
		padding: 0; }
	&:hover {
		& .b-step-stage__before {
			background-color: $accent-dark;
			opacity: 0.5; } }

	&__number {
		position: relative;
		z-index: 1;
		font: 48px $addition-font;
		margin-right: 25px; }
	&__date {
		margin-top: 13px; }

	&__before {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 140px;
		height: 58px;
		opacity: 0.8;
		background-color: $accent-dark;
		//background-color: $addition-lighter-colr
		@include border-radius(2em); }

	&.active {
		.b-step-stage__before {
			background-color: $accent-dark;
			opacity: 1; } }

	&:nth-child(1) {
		.b-step-stage__before {
			left: -85px;
			bottom: -55px;
			@include rotate(110deg); } }
	&:nth-child(2) {
		.b-step-stage__before {
			width: 161px;
			left: -33px;
			bottom: -70px;
			@include rotate(-130deg); } }
	&:nth-child(3) {
		.b-step-stage__before {
			width: 181px;
			left: -101px;
			bottom: -65px;
			@include rotate(-70deg); } }
	&:nth-child(4) {
		.b-step-stage__before {
			left: -55px;
			bottom: -74px;
			@include rotate(-90deg); } }


	@media screen and (max-width: $max-lg) {
		position: absolute;
		left: 20px;
		top: -60px;
		&.active {
			display: flex; }

		&__call-date {
			display: block;
			margin: 0;
			padding-right: 10px; } }
	@media screen and (min-width: $lg) {
		padding: 0 7% 0 0;
		flex: 0 0 25%;
		max-width: 25%;
		&__date {
			&--mobile {
				display: none; } } } }
