.c-page {
	display: flex;
	flex-direction: column;
	min-height: 100%; }

.c-main {
	flex: 1 0 auto; }
//HEADER
.c-header {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	z-index: 10;
	padding: 18px 20px;
	justify-content: space-between;
	align-items: center;
	@include row;

	&__navigation {
		display: flex;
		align-items: center;
		flex: 1;
		padding-right: 15px; }
	&__social {
		padding-left: 15px;
		padding-right: 15px;
		margin-top: 75px; }

	&__menu {
		display: none; }

	&__logo {
		position: relative; } }

//CONTENT
.c-content {
	padding: 50px 0 50px; }

//FOOTER
.c-footer {
	position: relative;
	flex: 0 0 auto;
	overflow: hidden;
	&__copyright {
		position: absolute;
		bottom: 0;
		right: 0;
		background-color: #ffffff;
		z-index: 1;
		font-size: 16px;
		padding: 32px 87px 31px 70px;
		border-top-left-radius: 6em;
		&:before {
			content: '';
			width: 0;
			height: 0;
			position: absolute;
			bottom: 99%;
			right: 0;
			border-top: 45px solid transparent;
			border-right: 257px solid #ffffff;
			border-bottom: 0 solid transparent; } }
	&__copyright-link {
		display: block;
		color: $accent; } }
.c-container {
	width: 90%;
	margin: 0 auto; }
.c-row {
	@include row;
	&--gutter {
		margin-left: -15px;
		margin-right: -15px; }
	&--gutter-30 {
		margin-left: -15px;
		margin-right: -15px; } }

.justify-content-center {
	justify-content: center; }
.justify-content-between {
	justify-content: space-between; }

.color-accent {
	color: $accent; }

@media screen and (max-width: $max-lg) {
	.c-header {
		&__navigation {
			padding: 0 20px;
			margin-top: auto;
			margin-bottom: 35px; }

		&__menu {
			position: fixed;
			left: -50%;
			top: 0;
			right: 0;
			bottom: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: #ffffff;
			opacity: 0;
			visibility: hidden;
			overflow-x: hidden;
			overflow-y: auto;
			padding-top: 75px;
			@include scale(.5);
			@include transition(all, .35s, ease);

			&.active {
				opacity: 1;
				visibility: visible;
				@include scale(1);
				left: 0; }

			&:before {
				content: '';
				position: absolute;
				right: -417px;
				top: 98px;
				width: 641px;
				height: 301px;
				@include rotate(-65deg);
				@include border-radius(30em);
				background-color: $accent-dark;
				opacity: 0.25; } } } }

@media screen and (min-width: $lg) {
	.c-container {
		width: 88%; }
	.c-row {
		&--gutter-30 {
			margin-left: -15px;
			margin-right: -15px; } }
	.c-header {
		padding: 20px 25px;
		&__burger {
			display: none; }

		&__menu {
			flex: 1;
			display: flex; }
		&__logo {
			padding-left: 15px; }
		&__social {
			margin-top: 0; } } }
