.b-participate {
	overflow: hidden;
	margin-bottom: 100px;
	&__item {
		position: relative;
		&--lg {
			flex: 0 0 100%;
			max-width: 100%;
			margin-bottom: 100px; }
		&--sm {
			flex: 0 0 100%;
			max-width: 100%; } }


	&__content {
		margin-top: -55px; }
	&__icon {
		margin-bottom: 4px; }
	&__title {
		margin-bottom: 65px; }
	&__button {
		margin-top: 85px; }
	&__before-xxl {
		top: -23%;
		left: 12%; } }
.b-participate-list {
	position: relative;
	padding-left: 65px;
	list-style: none;
	margin: 0;
	counter-reset: participateStep;
	&:before {
		content: '';
		position: absolute;
		top: 33%;
		left: 53px;
		width: 232px;
		height: 60px;
		background-color: $accent-light;
		opacity: 0.25;
		@include rotate(5deg);
		@include border-radius(2em); }
	&__item {
		position: relative;
		margin-bottom: 65px;
		&:last-child {
			margin-bottom: 0; }
		&:before {
			counter-increment: participateStep;
			content: counter(participateStep);
			position: absolute;
			top: -18px;
			left: -65px;
			font: 100px $addition-font;
			color: $accent-dark; }
		p {
			margin-bottom: 30px; } } }


@media screen and (max-width: $max-sm-l) {
	.b-participate {
		&__icon,
		&__title,
		&__button {
			text-align: center; } } }

@media screen and (min-width: $sm-l) {
	.b-participate {
		&__item {
			&--sm {
				flex: 0 0 30%;
				max-width: 30%;
				padding-left: 15px; }
			&--lg {
				flex: 0 0 60%;
				max-width: 60%;
				padding-right: 15px;
				margin-bottom: 0; } } }

	.b-participate-list {
		padding-left: 65px;
		&:before {
			left: -100px; } } }

@media screen and (min-width: 1200px) {
	.b-participate {
		margin-bottom: 200px;
		&__item {
			&--sm {
				flex: 0 0 25% {
					max-width: 25%; } } } } }
