.b-social {
	&__item {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: 40px;
		height: 40px;

		&:hover {
			opacity: 0.5; }

		.e-icon {
			font-size: 20px; } }
	//.b-social
	@media screen and (max-width: $max-lg) {
		display: flex;
		flex-direction: column;
		&__item {
			margin-bottom: 30px;
			.e-icon {
				color: $dark;
				font-size: 28px; } } } }
