.b-address {
	position: relative;
	font-size: 16px;
	margin-bottom: 25px;

	&--styl-1,
	&--styl-2,
	&--styl-3,
	&--styl-4,
	&--styl-5,
	&--styl-6 {
		&:before {
			content: '';
			position: absolute;
			width: 124px;
			height: 40px;
			background-color: $addition-light-color;
			opacity: 0.25;
			z-index: -1;

			@include border-radius(2em);
			@include transition(all, .4s, ease); } }

	&--styl-1 {
		&:before {
			top: 10px;
			left: -20px;
			transform: rotate(-10deg); } }

	&--styl-2 {
		&:before {
			width: 160px;
			height: 40px;
			top: -7px;
			right: 38px; } }
	&--styl-3 {
		&:before {
			width: 80px;
			height: 40px;
			top: -7px;
			left: 0;
			@include rotate(20deg); } }

	&--styl-4 {
		&:before {
			width: 120px;
			height: 40px;
			top: 20px;
			left: -26%;
			@include rotate(90deg); } }
	&--styl-5 {
		&:before {
			width: 160px;
			height: 40px;
			top: 0;
			right: 11%;
			@include rotate(10deg); } }

	&--styl-6 {
		&:before {
			width: 100px;
			height: 40px;
			left: 11%;
			@include rotate(-40deg); } }
	&__city-name {
		cursor: pointer;
		font: 32px $addition-font;
		margin-bottom: 10px;
		@include transition(all, .4s, ease); }
	&__stret {
		margin-bottom: 5px; }
	&__phone {
		display: block; }

	&:hover {
		.b-address__city-name {
			color: $accent; } }

	&.active {
		&:before {
			background-color: $accent; }
		.b-address__city-name {
			color: $accent; } } }
