.e-before {
	position: absolute;
	display: block;
	top: -44%;
	left: -9%;
	width: 232px;
	height: 60px;
	background-color: $addition-color;
	z-index: -1;
	@include border-radius(1.8em);
	@include rotate(-5deg);
	@include transition(all, .4s, ease);
	&--yellow {
		background-color: rgba($yellow, .8); }
	&--green {
		background-color: rgba($green, .55); }

	&--blue {
		background-color: rgba($blue, .25); }
	&--accent-dark {
		background-color: rgba($accent-dark, .25); }
	&--accent-light {
		background-color: rgba($accent-light, .55); }
	&--additional-color {
		opacity: 0.8;
		background-color: $addition-lighter-colr; }
	&--sm {
		width: 150px;
		height: 50px;
		@include border-radius(10em); }
	&--md {
		width: 451px;
		height: 51px;
		@include border-radius(10em); }
	&--md-l {
		width: 901px;
		height: 120px;
		@include border-radius(10em); }
	&--xl {
		width: 451px;
		height: 101px;
		@include border-radius(10em); }
	&--xxl {
		width: 901px;
		height: 241px;
		@include border-radius(10em); }
	&--xxxxl {
		width: 1200px;
		height: 554px;
		border-radius: 30em; }
	&--about {
		top: 0;
		left: 0;
		@include rotate(45deg); }
	&--about-md {
		top: -45px;
		left: 0px; }

	//.e-before
	@media screen and (min-width: $sm) {
		&--sm {
			width: 300px;
			height: 100px; }
		&--md {
			width: 701px;
			height: 70px; }
		&--xl {
			width: 701px;
			height: 141px; } }
	@media screen and (min-width: $lg) {
		&--md {
			width: 901px;
			height: 101px; }

		&--xl {
			width: 901px;
			height: 201px; }

		&--about-md {
			top: -98px;
			left: 13px; } } }

