
//Color
$accent: #d05685;
$accent-dark: #c9477f;
$accent-light: #ea8e99;
$addition-color: #5e4e6c;
$addition-lighter-colr: #a498b6;
$addition-light-color: #b5acc5;
$addition-darkness-color: #cdc7d7;
$blue: #508cbd;
$yellow: #fff2a0;
$green: #a1cabd;
$dark: #000000;
$main-silver: #d8d8d8;
$main-silver-2: #e8e5ed;

//Font
$default-font: 'MuseoSans', Helvetica, Arial, sans-serif;
$addition-font: 'Roska', Helvetica, Arial, sans-serif;

//Media queries
$xs: 481px;
$sm: 576px;
$sm-l: 768px;
$md: 993px;
$lg: 1025px;
$xl-g: 1050px;
$xl: 1367px;
$xxl: 1601px;
$xxxl: 1801px;


$max-sm: $sm - 1px;
$max-sm-l: $sm-l - 1px;
$max-lg: $lg - 1px;
$max-xl: $xl - 1px;
$max-xxl: $xxl - 1px;
$max-xxxl: $xxxl - 1px;

// Customization Bootstrap vars from "app/libs/bootstrap/scss/_variables.scss"
$grid-gutter-width: 30px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0, sm: 576px, md: 768px, lg: 1025px, xl: 1367px);
$container-max-widths: (sm: 540px, md: 730px, lg: 990px, xl: 1200px);

$gutter: $grid-gutter-width;
