.b-gallery {
	position: relative;
	overflow: hidden;
	padding-bottom: 70px;
	&__before-heart {
		position: absolute;
		right: -9%;
		top: 0;
		font-size: 485px; }
	&__item {
		position: relative;
		padding: 15px;
		flex: 1 1 366px;
		max-width: 366px;
		height: 398px;
		//flex: 0 0 100%
		//max-width: 100%
		img {
			width: 336px;
			height: 336px;
			object-fit: cover; } }
	&__footer {
		margin-top: 28px;
		max-width: 30%;
		margin-left: auto; }
	&__footer-before {
		left: inherit;
		top: inherit;
		left: -40%;
		bottom: -40px;
		@include rotate(10deg); }


	@media screen and (min-width: $md) {
		&__before-heart {
			top: -5%; } }
	@media screen and (min-width: $lg) {
		&__item {
			padding: 30px; }
		&__footer-before {
			bottom: -30px; } } }
