.e-close {
	position: relative;
	width: 30px;
	height: 30px;
	&:after,
	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 5px;
		left: -10px;
		right: 0;
		background: transparent;
		width: 6px;
		height: 2px;
		border-right: 13px solid $dark;
		border-left: 13px solid $dark;
		@include border-radius(2px); }
	&:before {
		@include rotate(46deg); }
	&:after {
		@include rotate(-46deg); } }
