.e-button {
	position: relative;
	display: inline-block;
	font: 700 14px $default-font;
	text-transform: uppercase;
	letter-spacing: 1px;
	color: #ffffff;
	padding: 16px 20px;
	background-color: rgba(#000000, 0.8);

	@include border-radius(1.8em);
	&:before {
		content: '';
		position: absolute;
		top: -44%;
		left: -9%;
		width: 100%;
		height: 100%;
		background-color: $addition-light-color;
		z-index: -1;
		@include border-radius(1.8em);
		@include rotate(-5deg);
		@include transition(all, .4s, ease); }
	&:hover,
	&:focus {
		&:before {
			@include rotate(0);
			top: 0;
			left: 0; } }

	&--log-in {
		padding: 13px 29px 13px 16px;
		display: flex;
		align-items: center;
		letter-spacing: 1px;
		background-image: linear-gradient(225deg, #8239fa 0%, #544ced 22%, #903acd 45%, #d53592 68%, #fbbb45 100%);
		&:before {
			display: none; } }

	//.e-button
	@media screen and (min-width: $lg) {
		font-size: 16px;
		padding: 19px 32px; } }
