.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  vertical-align: middle;
  fill: currentColor;
}

.icon--caret-right {
  font-size:(512/10)*1rem;
  width:(192/512)*1em;
}
.icon--fb {
  font-size:(512/10)*1rem;
  width:(264/512)*1em;
}
.icon--heart {
  font-size:(500/10)*1rem;
  width:(500/500)*1em;
}
.icon--instagram {
  font-size:(512/10)*1rem;
  width:(448/512)*1em;
}
.icon--marker {
  font-size:(512/10)*1rem;
  width:(384/512)*1em;
}
.icon--video {
  font-size:(512/10)*1rem;
  width:(448/512)*1em;
}
.icon--vk {
  font-size:(512/10)*1rem;
  width:(576/512)*1em;
}
