.b-caption {
	margin-bottom: 45px;
	color: $dark;

	&__before-icon {
		min-width: 81px;
		display: inline-block;
		text-align: center; }

	&__user {
		display: flex;
		align-items: center;
		margin-bottom: 32px;

		img {
			width: 48px;
			height: 48px;
			object-fit: cover; } }
	&__user-img {
		@include border-radius(50%);
		overflow: hidden; }
	&__user-name {
		margin: 0 0 0 15px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			right: 0;
			bottom: -7px;
			background-color: $main-silver-2;
			height: 2px;
			width: 100%;
			@include border-radius(2px); } }

	//.b-caption
	@media screen and (min-width: $lg) {
		margin-bottom: 75px;

		&--margin-b-40 {
			margin-bottom: 40px; } } }
