.b-burger {
	position: relative;
	width: 24px;
	height: 10px;
	transition-duration: 0.5s;
	z-index: 101;
	&:hover {
		cursor: pointer; }
	&:before {
		transition-duration: 0.5s;
		position: absolute;
		width: 24px;
		height: 2px;
		background-color: $dark;
		content: "";
		top: 0; }
	&:after {
		transition-duration: 0.5s;
		position: absolute;
		width: 24px;
		height: 2px;
		background-color: $dark;
		content: "";
		top: 10px; }
	&.active {
		&:before {
			-webkit-transform: rotateZ(45deg) scaleX(1.25) translate(1px, 4px);
			transform: rotateZ(45deg) scaleX(1.25) translate(1px, 4px);
			background-color: $dark; }
		&:after {
			-webkit-transform: rotateZ(-45deg) scaleX(1.25) translate(3px, -6px);
			transform: rotateZ(-45deg) scaleX(1.25) translate(3px, -6px);
			background-color: $dark; } } }
